"use client"

import { useState, useRef } from "react"
import Link from "next/link"
import {
  ArrowLeft,
  User,
  Mail,
  Phone,
  Lock,
  Calendar,
  Trash2,
  Upload,
  X,
  Monitor,
  Smartphone,
  Tablet,
  AlertCircle,
  Eye,
  EyeOff,
  Save,
  Camera,
} from "lucide-react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { InfoCard } from "./info-card"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import { useToast } from "@/hooks/use-toast"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar as CalendarComponent } from "@/components/ui/calendar"
import { format } from "date-fns"
import { ru } from "date-fns/locale"

interface LoginHistoryItem {
  id: string
  device: string
  deviceType: "desktop" | "mobile" | "tablet"
  browser: string
  location: string
  ip: string
  date: string
  time: string
}

const mockLoginHistory: LoginHistoryItem[] = [
  {
    id: "1",
    device: "Windows PC",
    deviceType: "desktop",
    browser: "Chrome 120.0",
    location: "Москва, Россия",
    ip: "192.168.1.1",
    date: "15 янв. 2024",
    time: "14:30",
  },
  {
    id: "2",
    device: "iPhone 13",
    deviceType: "mobile",
    browser: "Safari 17.0",
    location: "Санкт-Петербург, Россия",
    ip: "192.168.1.2",
    date: "10 янв. 2024",
    time: "09:15",
  },
  {
    id: "3",
    device: "iPad Air",
    deviceType: "tablet",
    browser: "Safari 17.0",
    location: "Москва, Россия",
    ip: "192.168.1.3",
    date: "5 янв. 2024",
    time: "16:45",
  },
]

const getDeviceIcon = (type: "desktop" | "mobile" | "tablet") => {
  switch (type) {
    case "desktop":
      return Monitor
    case "mobile":
      return Smartphone
    case "tablet":
      return Tablet
  }
}

export function MyProfile() {
  const { toast } = useToast()
  const fileInputRef = useRef<HTMLInputElement>(null)
  
  // Personal data
  const [personalData, setPersonalData] = useState({
    lastName: "Смирнов",
    firstName: "Андрей",
    middleName: "Иванович",
    birthDate: new Date(1990, 0, 15),
    phone: "+7 (999) 123-45-67",
  })
  
  // Password change
  const [passwordData, setPasswordData] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  })
  const [showPasswords, setShowPasswords] = useState({
    current: false,
    new: false,
    confirm: false,
  })
  
  // Email change
  const [emailData, setEmailData] = useState({
    currentEmail: "andrey.smirnov@example.com",
    newEmail: "",
  })
  
  // Avatar
  const [avatarUrl, setAvatarUrl] = useState<string | null>("/stylized-user-avatar.png")
  
  // Delete account
  const [deleteAccountOpen, setDeleteAccountOpen] = useState(false)
  const [deleteAvatarOpen, setDeleteAvatarOpen] = useState(false)

  const handlePersonalDataSave = () => {
    toast({
      title: "Данные сохранены",
      variant: "success",
    })
  }

  const handlePasswordChange = () => {
    if (passwordData.newPassword !== passwordData.confirmPassword) {
      toast({
        title: "Ошибка",
        description: "Пароли не совпадают",
        variant: "destructive",
      })
      return
    }
    if (passwordData.newPassword.length < 8) {
      toast({
        title: "Ошибка",
        description: "Пароль должен быть не менее 8 символов",
        variant: "destructive",
      })
      return
    }
    setPasswordData({
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
    })
    toast({
      title: "Пароль изменен",
      variant: "success",
    })
  }

  const handleEmailChange = () => {
    if (!emailData.newEmail || !emailData.newEmail.includes("@")) {
      toast({
        title: "Ошибка",
        description: "Введите корректный email адрес",
        variant: "destructive",
      })
      return
    }
    toast({
      title: "Email изменен",
      description: "На новый адрес отправлено письмо для подтверждения",
      variant: "success",
    })
    setEmailData({
      ...emailData,
      newEmail: "",
    })
  }

  const handleAvatarUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onloadend = () => {
        setAvatarUrl(reader.result as string)
        toast({
          title: "Аватар обновлен",
          variant: "success",
        })
      }
      reader.readAsDataURL(file)
    }
  }

  const handleDeleteAvatar = () => {
    setAvatarUrl(null)
    setDeleteAvatarOpen(false)
    toast({
      title: "Аватар удален",
      variant: "success",
    })
  }

  const handleDeleteAccount = () => {
    setDeleteAccountOpen(false)
    toast({
      title: "Аккаунт удален",
      variant: "destructive",
    })
  }

  const initials = `${personalData.lastName[0]}${personalData.firstName[0]}`

  return (
    <div className="min-h-screen bg-background">
      <div className="max-w-[896px] mx-auto px-8 py-6">
        <div className="space-y-8">
          {/* Header Section */}
          <div className="flex items-start justify-between gap-4" style={{ height: '56px' }}>
            <div className="flex-1">
              <div className="flex items-start gap-3">
                <Link href="/" className="pt-0.5">
                  <Button variant="outline" size="icon" className="h-8 w-8">
                    <ArrowLeft className="h-4 w-4" />
                  </Button>
                </Link>
                <div className="flex-1">
                  <h1 className="text-base font-semibold text-foreground">Мой профиль</h1>
                  <p className="text-sm text-muted-foreground mt-1">Управление личными данными и настройками аккаунта</p>
                </div>
              </div>
            </div>
          </div>

          {/* User Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
            <div className="flex items-center justify-between mb-6">
              <div className="flex items-center gap-4">
                <div className="relative group">
                  <Avatar className="h-16 w-16">
                    <AvatarImage src={avatarUrl || undefined} />
                    <AvatarFallback className="bg-gray-100 text-gray-700 text-lg">
                      {initials}
                    </AvatarFallback>
                  </Avatar>
                  <div className="absolute inset-0 bg-black/50 opacity-0 group-hover:opacity-100 rounded-full flex items-center justify-center transition-opacity cursor-pointer" onClick={() => fileInputRef.current?.click()}>
                    <Camera className="h-5 w-5 text-white" />
                  </div>
                </div>
                <div>
                  <p className="text-base font-semibold text-foreground">
                    {personalData.lastName} {personalData.firstName} {personalData.middleName}
                  </p>
                  <p className="text-sm text-muted-foreground mt-1">Администратор</p>
                </div>
              </div>
              <div className="flex items-center gap-2">
                {avatarUrl && (
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setDeleteAvatarOpen(true)}
                  >
                    <X className="h-4 w-4 mr-2" />
                    Удалить аватар
                  </Button>
                )}
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => fileInputRef.current?.click()}
                >
                  <Upload className="h-4 w-4 mr-2" />
                  Сменить аватар
                </Button>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="image/*"
                  className="hidden"
                  onChange={handleAvatarUpload}
                />
              </div>
            </div>
          </div>

          {/* Two Column Layout */}
          <div className="grid grid-cols-2 gap-8">
            {/* Personal Data */}
            <InfoCard icon={User} title="Персональные данные">
              <div className="space-y-4 py-2">
                <div>
                  <Label htmlFor="lastName" className="text-sm text-muted-foreground mb-1.5 block">Фамилия</Label>
                  <Input
                    id="lastName"
                    value={personalData.lastName}
                    onChange={(e) => setPersonalData({ ...personalData, lastName: e.target.value })}
                    className="w-full"
                  />
                </div>
                <div>
                  <Label htmlFor="firstName" className="text-sm text-muted-foreground mb-1.5 block">Имя</Label>
                  <Input
                    id="firstName"
                    value={personalData.firstName}
                    onChange={(e) => setPersonalData({ ...personalData, firstName: e.target.value })}
                    className="w-full"
                  />
                </div>
                <div>
                  <Label htmlFor="middleName" className="text-sm text-muted-foreground mb-1.5 block">Отчество</Label>
                  <Input
                    id="middleName"
                    value={personalData.middleName}
                    onChange={(e) => setPersonalData({ ...personalData, middleName: e.target.value })}
                    className="w-full"
                  />
                </div>
                <div>
                  <Label htmlFor="birthDate" className="text-sm text-muted-foreground mb-1.5 block">Дата рождения</Label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className="w-full justify-start text-left font-normal"
                      >
                        <Calendar className="mr-2 h-4 w-4" />
                        {personalData.birthDate ? (
                          format(personalData.birthDate, "PPP", { locale: ru })
                        ) : (
                          <span>Выберите дату</span>
                        )}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <CalendarComponent
                        mode="single"
                        selected={personalData.birthDate}
                        onSelect={(date) => date && setPersonalData({ ...personalData, birthDate: date })}
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                </div>
                <div>
                  <Label htmlFor="phone" className="text-sm text-muted-foreground mb-1.5 block">Номер телефона</Label>
                  <Input
                    id="phone"
                    value={personalData.phone}
                    onChange={(e) => setPersonalData({ ...personalData, phone: e.target.value })}
                    className="w-full"
                    type="tel"
                  />
                </div>
                <Button onClick={handlePersonalDataSave} className="w-full gap-2">
                  <Save className="h-4 w-4" />
                  Сохранить изменения
                </Button>
              </div>
            </InfoCard>

            {/* Password Change */}
            <InfoCard icon={Lock} title="Смена пароля">
              <div className="space-y-4 py-2">
                <div>
                  <Label htmlFor="currentPassword" className="text-sm text-muted-foreground mb-1.5 block">Текущий пароль</Label>
                  <div className="relative">
                    <Input
                      id="currentPassword"
                      type={showPasswords.current ? "text" : "password"}
                      value={passwordData.currentPassword}
                      onChange={(e) => setPasswordData({ ...passwordData, currentPassword: e.target.value })}
                      className="w-full pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="absolute right-0 top-0 h-full w-9"
                      onClick={() => setShowPasswords({ ...showPasswords, current: !showPasswords.current })}
                    >
                      {showPasswords.current ? (
                        <EyeOff className="h-4 w-4" />
                      ) : (
                        <Eye className="h-4 w-4" />
                      )}
                    </Button>
                  </div>
                </div>
                <div>
                  <Label htmlFor="newPassword" className="text-sm text-muted-foreground mb-1.5 block">Новый пароль</Label>
                  <div className="relative">
                    <Input
                      id="newPassword"
                      type={showPasswords.new ? "text" : "password"}
                      value={passwordData.newPassword}
                      onChange={(e) => setPasswordData({ ...passwordData, newPassword: e.target.value })}
                      className="w-full pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="absolute right-0 top-0 h-full w-9"
                      onClick={() => setShowPasswords({ ...showPasswords, new: !showPasswords.new })}
                    >
                      {showPasswords.new ? (
                        <EyeOff className="h-4 w-4" />
                      ) : (
                        <Eye className="h-4 w-4" />
                      )}
                    </Button>
                  </div>
                </div>
                <div>
                  <Label htmlFor="confirmPassword" className="text-sm text-muted-foreground mb-1.5 block">Подтвердите новый пароль</Label>
                  <div className="relative">
                    <Input
                      id="confirmPassword"
                      type={showPasswords.confirm ? "text" : "password"}
                      value={passwordData.confirmPassword}
                      onChange={(e) => setPasswordData({ ...passwordData, confirmPassword: e.target.value })}
                      className="w-full pr-10"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="icon"
                      className="absolute right-0 top-0 h-full w-9"
                      onClick={() => setShowPasswords({ ...showPasswords, confirm: !showPasswords.confirm })}
                    >
                      {showPasswords.confirm ? (
                        <EyeOff className="h-4 w-4" />
                      ) : (
                        <Eye className="h-4 w-4" />
                      )}
                    </Button>
                  </div>
                </div>
                <Button onClick={handlePasswordChange} className="w-full gap-2">
                  <Save className="h-4 w-4" />
                  Изменить пароль
                </Button>
              </div>
            </InfoCard>

            {/* Email Change */}
            <InfoCard icon={Mail} title="Смена email адреса">
              <div className="space-y-4 py-2">
                <div>
                  <Label htmlFor="currentEmail" className="text-sm text-muted-foreground mb-1.5 block">Текущий email</Label>
                  <Input
                    id="currentEmail"
                    value={emailData.currentEmail}
                    disabled
                    className="w-full bg-muted"
                  />
                </div>
                <div>
                  <Label htmlFor="newEmail" className="text-sm text-muted-foreground mb-1.5 block">Новый email</Label>
                  <Input
                    id="newEmail"
                    type="email"
                    value={emailData.newEmail}
                    onChange={(e) => setEmailData({ ...emailData, newEmail: e.target.value })}
                    placeholder="example@email.com"
                    className="w-full"
                  />
                </div>
                <Button onClick={handleEmailChange} className="w-full gap-2">
                  <Save className="h-4 w-4" />
                  Изменить email
                </Button>
              </div>
            </InfoCard>

            {/* Login History */}
            <InfoCard icon={Monitor} title="История входа">
              <div className="py-2">
                <div className="space-y-3">
                  {mockLoginHistory.map((item) => {
                    const DeviceIcon = getDeviceIcon(item.deviceType)
                    return (
                      <div key={item.id} className="flex items-center gap-3 pb-3 border-b border-gray-200 last:border-0">
                        <div className="flex items-center justify-center w-10 h-10 bg-gray-50 rounded-lg flex-shrink-0">
                          <DeviceIcon className="h-5 w-5 text-muted-foreground" />
                        </div>
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-medium text-foreground">{item.device}</p>
                          <p className="text-xs text-muted-foreground mt-0.5">
                            {item.browser} • {item.location}
                          </p>
                          <p className="text-xs text-muted-foreground mt-0.5">
                            {item.date} в {item.time} • IP: {item.ip}
                          </p>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </div>
            </InfoCard>
          </div>

          {/* Delete Account */}
          <InfoCard icon={AlertCircle} title="Удаление аккаунта">
            <div className="py-2">
              <p className="text-sm text-muted-foreground mb-4">
                Удаление аккаунта является необратимым действием. Все ваши данные будут удалены без возможности восстановления.
              </p>
              <Button
                variant="destructive"
                onClick={() => setDeleteAccountOpen(true)}
                className="gap-2"
              >
                <Trash2 className="h-4 w-4" />
                Удалить аккаунт
              </Button>
            </div>
          </InfoCard>
        </div>
      </div>

      {/* Delete Avatar Dialog */}
      <AlertDialog open={deleteAvatarOpen} onOpenChange={setDeleteAvatarOpen}>
        <AlertDialogContent className="sm:max-w-[512px]">
          <AlertDialogHeader>
            <div className="flex items-center gap-2">
              <AlertCircle className="h-5 w-5 text-red-500 flex-shrink-0" />
              <AlertDialogTitle className="text-left">Удалить аватар?</AlertDialogTitle>
            </div>
            <AlertDialogDescription className="text-left mt-2">
              Аватар будет удален без возможности восстановления. Вы сможете загрузить новый аватар в любой момент.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter className="sm:justify-end">
            <AlertDialogCancel>Отмена</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteAvatar}
              className="bg-red-600 hover:bg-red-700 text-white"
            >
              Удалить
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Delete Account Dialog */}
      <AlertDialog open={deleteAccountOpen} onOpenChange={setDeleteAccountOpen}>
        <AlertDialogContent className="sm:max-w-[512px]">
          <AlertDialogHeader>
            <div className="flex items-center gap-2">
              <AlertCircle className="h-5 w-5 text-red-500 flex-shrink-0" />
              <AlertDialogTitle className="text-left">Удалить аккаунт?</AlertDialogTitle>
            </div>
            <AlertDialogDescription className="text-left mt-2">
              Это действие является необратимым. Все ваши данные, включая персональную информацию, документы и историю активности, будут удалены навсегда. Пожалуйста, убедитесь, что вы хотите продолжить.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter className="sm:justify-end">
            <AlertDialogCancel>Отмена</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteAccount}
              className="bg-red-600 hover:bg-red-700 text-white"
            >
              Удалить аккаунт
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  )
}

